import { FC, useMemo } from 'react';
import { useSelector } from 'react-redux';

import styled from 'styled-components';

import { enterpriseCustomIconId } from '@common/utils/test-ids';

import { selectTargetTheming } from '@common/slices/target-theming';

import { CustomIcon } from '@common/components/Icon/CustomIcon';

import useSystemSchemeCheck from '@common/hooks/useSystemSchemeCheck';

type BrandLogoContainerProps = {
    size: 'small' | 'large';
};

const Icon = styled(CustomIcon)`
    min-width: 100%;
    min-height: 100%;
    width: 100%;
    height: 100%;
`;

const BrandIcon: FC<BrandLogoContainerProps> = ({ size }) => {
    const theme = useSelector(selectTargetTheming);
    const { isSchemeDark } = useSystemSchemeCheck();

    const currentTheme = useMemo(() => {
        if (theme) {
            return isSchemeDark ? theme.theme.dark : theme.theme.light;
        }
    }, [theme, isSchemeDark]);

    const iconUrl = useMemo(() => currentTheme?.brand?.icons?.symbol, [currentTheme]);

    return (
        <>
            {iconUrl && (
                <span
                    role="img"
                    aria-label={`Company Logo`}
                    style={{ display: 'inline-block', width: '100%', height: '100%' }}
                >
                    <Icon size={size} iconURL={iconUrl} testId={enterpriseCustomIconId} />
                </span>
            )}
        </>
    );
};

export default BrandIcon;
