import { useCallback, useEffect, useRef, useState } from 'react';

import OpenFin from '@openfin/core';

import {
    BaseEnterpriseMenuChannelMessage,
    ContextMenuChannelMessage,
    EnterpriseMenuType,
    RenameSupertabChannelMessage
} from '@common/utils/popup-window';
import { getCurrentOFWindow, WindowName } from '@common/utils/window';

export type ContextMenuWindowMessage = ContextMenuChannelMessage | RenameSupertabChannelMessage;

/*
 * This hook is used to manage the popup window state and show the popup window.
 * @param windowName - The name of the popup window.
 * @param createChannel - The function to create the channel.
 * @param showPopupOverride - The function to show the popup window. If not provided, the default behavior will be used to show the popup window.
 * @param options - An object containing optional parameters.
 * @param options.blurBehavior - The behavior of the popup window when it loses focus. Can be 'hide' or 'modal'. Defaults to 'hide'.
 * @param options.dispatchDirectlyToChannel - If true, the response will be dispatched directly to the channel. If false, the response will be dispatched to the parent window.
 * @param options.forwardTopicName - The topic name to forward the response to.
 */
export const useManagePopupWindow = <T extends BaseEnterpriseMenuChannelMessage>(
    windowName: string,
    createChannel: () => Promise<OpenFin.ChannelProvider>,
    showPopupOverride?: (bounds: OpenFin.Bounds) => Promise<void>,
    options?: {
        blurBehavior?: 'hide' | 'modal';
        dispatchDirectlyToChannel?: boolean;
        forwardTopicName?: string;
    }
) => {
    const channelToParent = useRef<OpenFin.ChannelProvider | null>(null);
    const [payload, setPayload] = useState<T | null>(null);

    const handlePopupRequest = useCallback(async (ev: T) => {
        const window = fin.me as OpenFin.Window;

        // Assuming a popup window is currently active, if a user attempts to activate the popup window elsewhere, we want to
        // first blur the popup window to emit a response for the first invocation before setting the payload to re-show.
        if (await window.isShowing()) {
            // Do not blur the zoom controls dialog in this instance. The dialog should stay open on successive show calls.
            if (ev.type !== EnterpriseMenuType.ZoomControls) {
                await window.blur();
            }
        }
        setPayload(ev);
    }, []);

    const showPopup = useCallback(
        async (bounds: OpenFin.Bounds) => {
            if (!payload) return;
            const popupWindow = fin.Window.wrapSync({ name: windowName, uuid: fin.me.uuid });
            const parentWindow = fin.Window.wrapSync(payload.parentIdentity);

            // SAAS-2980 - Windows Limitation:
            // - when showTaskbarIcon: false - the window is visible on all desktops
            // - to prevent this, we set a parentIdentity on the popupWindow
            // - we need to keep it alive after the parent window is closed,
            // so on hidden we set the parentIdentity to a non-existent window identity
            // which in core will unset the parent.

            // always set the parent identity during showPopup
            await popupWindow.updateOptions({
                // @ts-expect-error type mismatch
                experimental: { parentIdentity: payload.parentIdentity }
            });

            await popupWindow.prependOnceListener('hidden', async () => {
                // bookmarks dialog "child" popup window has a parent of the bookmarks dialog,
                // skip it for that case
                if (payload.parentIdentity.name !== WindowName.EnterpriseBookmarkDialogWindow) {
                    await popupWindow.updateOptions({
                        // @ts-expect-error type mismatch
                        experimental: { parentIdentity: { uuid: '__NON_EXISTENT', name: '__NON_EXISTENT' } }
                    });
                }
            });

            if (showPopupOverride) {
                await showPopupOverride(bounds);
                return;
            }

            try {
                const { height, width, top, left } = bounds;

                (fin.me as OpenFin.Window).once('blurred', () => setPayload(null));
                const response = await parentWindow.showPopupWindow({
                    name: windowName,
                    resultDispatchBehavior: 'hide',
                    blurBehavior: options?.blurBehavior ?? 'hide',
                    hideOnClose: true,
                    focus: true,
                    height,
                    width,
                    x: left,
                    y: top,
                    additionalOptions: {
                        alwaysOnTop: true,
                        contextMenuOptions: {
                            enabled: true,
                            template: ['inspect']
                        }
                    }
                });

                const shouldTargetRequestId =
                    'requestId' in payload &&
                    (payload.type === EnterpriseMenuType.ContextMenu || payload.type === EnterpriseMenuType.GlobalMenu);

                // Context menu invocations coming from the provider window should target a topic with a specific requestId.
                const responseTopic = shouldTargetRequestId ? `response-${payload.requestId}` : 'response';
                await channelToParent.current?.dispatch(payload.responseIdentity, responseTopic, response);

                if (options?.forwardTopicName) {
                    channelToParent.current?.publish(options.forwardTopicName, response);
                }
            } catch (error) {
                console.error('Error showing popup:', error);
            }
        },
        [payload]
    );

    const onResponse = useCallback(
        async (data?: any) => {
            try {
                if (options?.dispatchDirectlyToChannel) {
                    if (payload) {
                        await channelToParent.current?.dispatch(payload.responseIdentity, 'response', data);

                        if (options.forwardTopicName) {
                            Promise.all(channelToParent.current?.publish(options.forwardTopicName, data)).catch(() => {
                                /* Channel that listens to forwarding messages is not open */
                            });
                        }
                    }

                    if (data.type === 'cancel' || data.type === 'dropdown-blurred') {
                        setPayload(null);
                    }
                } else {
                    await getCurrentOFWindow().dispatchPopupResult(data);

                    if (payload) {
                        fin.Window.wrapSync(payload.parentIdentity).focus();
                        setPayload(null);
                    }
                }
            } catch (error) {
                console.error('Error handling response:', error);
            }
        },
        [payload]
    );

    useEffect(() => {
        const setupChannel = async () => {
            const channel = await createChannel();
            channel.register('open', handlePopupRequest);
            channelToParent.current = channel;
        };

        try {
            setupChannel();
        } catch (error) {
            console.error('Error setting up channel:', error);
        }

        return () => {
            if (channelToParent.current) {
                channelToParent.current.destroy();
                channelToParent.current = null;
            }
        };
    }, [handlePopupRequest, createChannel]);

    return {
        channelRef: channelToParent,
        payload,
        showPopup,
        onResponse
    };
};
