import { FC } from 'react';

import { withRouter } from 'next/router';

import { createContextMenuChannel } from '@common/utils/enterprise-channels';
import { EnterpriseMenuType } from '@common/utils/popup-window';
import { Router } from '@common/utils/router';
import { WindowName } from '@common/utils/window';

import ContextMenu from '@enterprise/components/App/ContextMenu/ContextMenu';
import RenameSupertabInput from '@enterprise/components/App/ContextMenu/RenameSupertabInput';
import BrowserThemeProvider from '@enterprise/components/App/ThemeProvider/BrowserThemeProvider';

import { useIsWindows } from '@enterprise/hooks/useIsWindows';
import { ContextMenuWindowMessage, useManagePopupWindow } from '@enterprise/hooks/useManagePopupWindow';

interface Props {
    router: Router;
}

const ContextMenuPopup: FC<Props> = ({ router }) => {
    const isWindows = useIsWindows();

    const { payload, showPopup, onResponse } = useManagePopupWindow<ContextMenuWindowMessage>(
        WindowName.EnterpriseContextMenu,
        createContextMenuChannel,
        undefined,
        { blurBehavior: 'hide' }
    );

    const renderContent = () => {
        switch (payload?.type) {
            case EnterpriseMenuType.GlobalMenu:
            case EnterpriseMenuType.ContextMenu:
                return (
                    <ContextMenu
                        messageData={payload}
                        onResponse={onResponse}
                        showPopup={showPopup}
                        isWindows={isWindows}
                    />
                );
            case EnterpriseMenuType.RenameSupertab:
                return (
                    <RenameSupertabInput
                        messageData={payload}
                        onResponse={onResponse}
                        showPopup={showPopup}
                        isWindows={isWindows}
                    />
                );
            default:
                return <></>;
        }
    };

    return <BrowserThemeProvider>{renderContent()}</BrowserThemeProvider>;
};

export default withRouter(ContextMenuPopup);
