import { useEffect, useRef, useState } from 'react';

interface UseLoadCustomIconResult {
    dynamicDimensions: { width: string; height: string } | null;
    isLoading: boolean;
    hasError: boolean;
}

interface UseLoadCustomIconOptions {
    iconURL?: string;
    useDynamicDimensions?: boolean;
    customIconAccessible?: boolean;
    maxHeight?: number;
}

/**
 * Custom hook that loads an image and extracts its dimensions for dynamic sizing
 * @param options Configuration options for the hook
 * @returns Object containing dynamic dimensions, loading state, and error state
 */
export const useLoadCustomIcon = ({
    iconURL,
    useDynamicDimensions = false,
    customIconAccessible = false,
    maxHeight = 36
}: UseLoadCustomIconOptions): UseLoadCustomIconResult => {
    const [dynamicDimensions, setDynamicDimensions] = useState<{ width: string; height: string } | null>(null);
    const [isLoading, setIsLoading] = useState(false);
    const [hasError, setHasError] = useState(false);
    const imageRef = useRef<HTMLImageElement | null>(null);

    useEffect(() => {
        // Reset states when dependencies change
        setDynamicDimensions(null);
        setIsLoading(false);
        setHasError(false);

        if (!useDynamicDimensions || !iconURL || !customIconAccessible) {
            return;
        }

        setIsLoading(true);
        const img = new Image();
        imageRef.current = img;

        const handleLoad = () => {
            if (img.naturalWidth && img.naturalHeight) {
                // Calculate dimensions with max height constraint
                const aspectRatio = img.naturalWidth / img.naturalHeight;
                let finalWidth = img.naturalWidth;
                let finalHeight = img.naturalHeight;

                // Apply max height constraint while maintaining aspect ratio
                if (finalHeight > maxHeight) {
                    finalHeight = maxHeight;
                    finalWidth = maxHeight * aspectRatio;
                }

                setDynamicDimensions({
                    width: `${Math.round(finalWidth)}px`,
                    height: `${Math.round(finalHeight)}px`
                });
            }
            setIsLoading(false);
        };

        const handleError = () => {
            setHasError(true);
            setIsLoading(false);
        };

        img.onload = handleLoad;
        img.onerror = handleError;
        img.src = iconURL;

        return () => {
            img.onload = null;
            img.onerror = null;
        };
    }, [iconURL, customIconAccessible, useDynamicDimensions, maxHeight]);

    return {
        dynamicDimensions,
        isLoading,
        hasError
    };
};
