import { useEffect, useState } from 'react';
import { useSelector } from 'react-redux';

import { ColorScheme, ColorSchemeType } from '@openfin/ui-library';

import { selectCurrentScheme } from '@common/slices/target-theming';

/**
 * Custom hook that checks the system color scheme and provides information about the current scheme.
 *
 * @returns An object containing the current color scheme, system color scheme, and a boolean indicating if the scheme is dark.
 */
interface SystemSchemeCheck {
    currentScheme: Omit<ColorSchemeType, 'system'>;
    systemScheme: Omit<ColorSchemeType, 'system'>;
    isSchemeDark: boolean;
}

function useSystemSchemeCheck(): SystemSchemeCheck {
    const [isSchemeDark, setIsSchemeDark] = useState(false);
    const currentScheme = useSelector(selectCurrentScheme);

    // Check the initial value
    const getSystemScheme = (): 'light' | 'dark' => {
        if (typeof window !== 'undefined') {
            return window.matchMedia('(prefers-color-scheme: dark)').matches ? 'dark' : 'light';
        }
        return 'dark'; // Default to 'dark' if window is not defined
    };

    // Initialize state
    const [systemScheme, setSystemScheme] = useState<Omit<ColorSchemeType, 'system'>>(getSystemScheme);

    useEffect(() => {
        // Create a handler for changes in the media query
        const mediaQueryList = window.matchMedia('(prefers-color-scheme: dark)');
        const handleChange = (e: MediaQueryListEvent) => {
            setSystemScheme(e.matches ? 'dark' : 'light');
        };

        // Add the event listener
        mediaQueryList.addEventListener('change', handleChange);

        // Cleanup listener on component unmount
        return () => {
            mediaQueryList.removeEventListener('change', handleChange);
        };
    }, []);

    useEffect(() => {
        switch (currentScheme) {
            case ColorScheme.system:
                setIsSchemeDark(systemScheme === ColorScheme.dark);
                break;
            case ColorScheme.dark:
                setIsSchemeDark(true);
                break;
            case ColorScheme.light:
                setIsSchemeDark(false);
                break;
        }
    }, [systemScheme, currentScheme]);

    return { systemScheme, isSchemeDark, currentScheme };
}

export default useSystemSchemeCheck;
